import { ComponentStylingModel, ComponentStylingPropertiesControlComponent, genericPropertiesControlFor } from '@anglr/dynamic/layout-editor';
import { MaterialTextareaModel } from './textarea.model';
/**
 * Material textarea layout metadata
 */

export class MaterialTextareaLayoutEditorMetadata {
  //######################### constructor #########################
  constructor() {
    //######################### public properties - implementation of LayoutEditorMetadataDescriptor #########################

    /**
     * @inheritdoc
     */
    this.metaInfo = {
      name: 'Textarea',
      description: 'Material textarea',
      optionsMetadata: {
        propertiesMetadata: [{
          modelType: ComponentStylingModel,
          propertiesControls: [ComponentStylingPropertiesControlComponent]
        }, {
          modelType: MaterialTextareaModel,
          propertiesControls: [genericPropertiesControlFor(['label', 'placeholder', 'hint', 'appearance', 'controlName'])]
        }]
      },
      defaultOptions: {
        label: 'Input label',
        placeholder: 'Placeholder',
        hint: 'Hint'
      },
      group: 'Material form fields'
    };
    Object.freeze(this);
  }

}